// components/TodoList.tsx
"use client";
import React, { useEffect, useState, useMemo, useRef } from "react";
import {
  DndContext,
  closestCenter,
  closestCorners,
  PointerSensor,
  useSensor,
  useSensors,
} from "@dnd-kit/core";
import {
  arrayMove,
  SortableContext,
  verticalListSortingStrategy,
  rectSortingStrategy,
  useSortable,
} from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";
import { useRouter } from "next/navigation";
import { useAuth } from "./AuthProvider";
import {
  fetchTodos,
  createTodo,
  updateTodo,
  deleteTodo,
  reorderTodos,
} from "../lib/auth";

type Todo = {
  id: string | number;
  title: string;
  description?: string;
  priority?: "extreme" | "moderate" | "low";
  is_completed?: boolean;
  position?: number;
  todo_date?: string;
  created_at?: string;
  updated_at?: string;
};

/* -------------------------
   Sortable Item (task card)
   ------------------------- */
function SortableItem({
  todo,
  onEdit,
  onDelete,
  onToggleComplete,
}: {
  todo: Todo;
  onEdit: (t: Todo) => void;
  onDelete: (id: string) => void;
  onToggleComplete: (t: Todo) => void;
}) {
  const { attributes, listeners, setNodeRef, transform, transition } =
    useSortable({ id: todo.id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  const priorityConfig = {
    extreme: { bg: "bg-pink-50", border: "border-pink-200", text: "text-pink-700", label: "Extreme" },
    moderate: { bg: "bg-green-50", border: "border-green-200", text: "text-green-700", label: "Moderate" },
    low: { bg: "bg-yellow-50", border: "border-yellow-200", text: "text-yellow-700", label: "Low" },
  };

  const priority = todo.priority || "moderate";
  const config = priorityConfig[priority];

  const formatDate = (dateString?: string) => {
    if (!dateString) return "";
    const date = new Date(dateString);
    const months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
    return `Due ${months[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`;
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className="bg-white rounded-lg border border-gray-200 shadow-sm p-5 hover:shadow-md transition-all relative h-full flex flex-col cursor-grab active:cursor-grabbing"
    >
      {/* Drag Handle - Entire card is draggable except buttons */}
      <div
        {...attributes}
        {...listeners}
        className="absolute inset-0 cursor-grab active:cursor-grabbing z-0"
        style={{ touchAction: 'none' }}
      />
      
      {/* Visible drag indicator */}
      <div className="absolute top-3 right-3 text-gray-400 pointer-events-none z-20">
        <svg className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <path d="M10 6h.01M14 6h.01M10 12h.01M14 12h.01M10 18h.01M14 18h.01" strokeLinecap="round" strokeLinejoin="round"/>
        </svg>
      </div>

      <div className="flex flex-col h-full relative z-10">
        <div className="flex-1 min-h-0">
          <h3 className="text-lg font-semibold text-gray-900 mb-2 pr-8">{todo.title}</h3>
          <p className="text-sm text-gray-600 mb-4 line-clamp-3">{todo.description || ""}</p>
        </div>
        
        <div className="flex items-center justify-between mt-auto pt-4 border-t border-gray-100">
          <div className="flex flex-col gap-2 flex-1">
            <span className={`text-xs font-semibold px-3 py-1 rounded-lg border ${config.bg} ${config.border} ${config.text} flex items-center gap-1 w-fit`}>
              <svg className="w-3 h-3" viewBox="0 0 24 24" fill="currentColor">
                <path d="M3 3h8v8H3V3zm10 0h8v8h-8V3zM3 13h8v8H3v-8zm10 0h8v8h-8v-8z"/>
              </svg>
              {config.label}
            </span>
            
            {todo.todo_date && (
              <span className="text-xs text-gray-500">{formatDate(todo.todo_date)}</span>
            )}
          </div>

          <div className="flex items-center gap-2 relative z-30">
            <button
              onClick={(e) => {
                e.stopPropagation();
                e.preventDefault();
                onEdit(todo);
              }}
              onMouseDown={(e) => {
                e.stopPropagation();
                e.preventDefault();
              }}
              onTouchStart={(e) => {
                e.stopPropagation();
              }}
              className="p-2 text-blue-600 hover:bg-blue-50 rounded transition relative z-30"
              title="Edit"
            >
              <svg className="w-4 h-4" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
              </svg>
            </button>
            <button
              onClick={(e) => {
                e.stopPropagation();
                e.preventDefault();
                onDelete(String(todo.id));
              }}
              onMouseDown={(e) => {
                e.stopPropagation();
                e.preventDefault();
              }}
              onTouchStart={(e) => {
                e.stopPropagation();
              }}
              className="p-2 text-red-600 hover:bg-red-50 rounded transition relative z-30"
              title="Delete"
            >
              <svg className="w-4 h-4" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M3 6h18M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
              </svg>
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

/* -------------------------
   Main Component
   ------------------------- */
export default function TodoList() {
  const auth = useAuth();
  const router = useRouter();
  const [todos, setTodos] = useState<Todo[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchQuery, setSearchQuery] = useState("");
  const [filterOpen, setFilterOpen] = useState(false);
  const [dateFilter, setDateFilter] = useState<string[]>([]);

  // add modal state for "Add New Task"
  const [showAddModal, setShowAddModal] = useState(false);
  const [createForm, setCreateForm] = useState({
    title: "",
    description: "",
    priority: "moderate" as "extreme" | "moderate" | "low",
    todo_date: "",
  });
  const dateInputRef = useRef<HTMLInputElement>(null);

  // edit modal
  const [editingTodo, setEditingTodo] = useState<Todo | null>(null);
  const [editForm, setEditForm] = useState({
    title: "",
    description: "",
    priority: "moderate" as "extreme" | "moderate" | "low",
    todo_date: "",
  });
  const editDateInputRef = useRef<HTMLInputElement>(null);

  const sensors = useSensors(
    useSensor(PointerSensor, {
      activationConstraint: {
        distance: 5,
      },
    })
  );

  // Filter and search logic
  const filteredTodos = useMemo(() => {
    let filtered = [...todos];

    // Search filter
    if (searchQuery.trim()) {
      const query = searchQuery.toLowerCase();
      filtered = filtered.filter(
        (todo) =>
          todo.title.toLowerCase().includes(query) ||
          todo.description?.toLowerCase().includes(query)
      );
    }

    // Date filter
    if (dateFilter.length > 0) {
      const today = new Date();
      today.setHours(0, 0, 0, 0);

      filtered = filtered.filter((todo) => {
        if (!todo.todo_date) return false;
        const todoDate = new Date(todo.todo_date);
        todoDate.setHours(0, 0, 0, 0);

        return dateFilter.some((filter) => {
          if (filter === "today") {
            return todoDate.getTime() === today.getTime();
          }
          if (filter === "5days") {
            const fiveDaysLater = new Date(today);
            fiveDaysLater.setDate(today.getDate() + 5);
            return todoDate >= today && todoDate <= fiveDaysLater;
          }
          if (filter === "10days") {
            const tenDaysLater = new Date(today);
            tenDaysLater.setDate(today.getDate() + 10);
            return todoDate >= today && todoDate <= tenDaysLater;
          }
          if (filter === "30days") {
            const thirtyDaysLater = new Date(today);
            thirtyDaysLater.setDate(today.getDate() + 30);
            return todoDate >= today && todoDate <= thirtyDaysLater;
          }
          return false;
        });
      });
    }

    // Sort by position (default - for drag and drop)
    filtered.sort((a, b) => (a.position ?? 0) - (b.position ?? 0));

    return filtered;
  }, [todos, searchQuery, dateFilter]);

  const toggleDateFilter = (filter: string) => {
    setDateFilter((prev) =>
      prev.includes(filter)
        ? prev.filter((f) => f !== filter)
        : [...prev, filter]
    );
  };

  // redirect if not auth
  useEffect(() => {
    if (!auth?.user || !auth?.token) {
      router.push("/login");
      return;
    }
  }, [auth, router]);

  // fetch todos
  useEffect(() => {
    if (!auth?.token) return;

    fetchTodos()
      .then((res) => {
        const arr = Array.isArray(res) ? res : res.results || [];
        const sorted = arr.sort((a: any, b: any) => (a.position ?? 0) - (b.position ?? 0));
        setTodos(sorted);
      })
      .catch((err) => {
        console.error("fetchTodos error:", err);
        setTodos([]);
      })
      .finally(() => setLoading(false));
  }, [auth?.token]);

  // drag end
  const handleDragEnd = async (event: any) => {
    const { active, over } = event;
    if (!over || active.id === over.id) return;

    // Find indices in the original todos array
    const oldIndex = todos.findIndex((t) => t.id === active.id);
    const newIndex = todos.findIndex((t) => t.id === over.id);

    if (oldIndex === -1 || newIndex === -1) return;

    // Reorder the todos array
    const newList = arrayMove(todos, oldIndex, newIndex);
    
    // Update positions in the new list
    const updatedList = newList.map((todo, index) => ({
      ...todo,
      position: index + 1,
    }));
    
    // Update state immediately for better UX
    setTodos(updatedList);
    
    try {
      // Send position updates to server
      await reorderTodos(
        updatedList.map((t) => ({
          id: t.id,
          position: t.position,
        }))
      );
      
      // Refresh todos from server to ensure sync
      const res = await fetchTodos();
      const arr = Array.isArray(res) ? res : res.results || [];
      const sorted = arr.sort((a: any, b: any) => (a.position ?? 0) - (b.position ?? 0));
      setTodos(sorted);
    } catch (err) {
      console.error("reorder error:", err);
      // Revert on error
      setTodos(todos);
    }
  };

  // create
  const handleCreate = async () => {
    if (!createForm.title.trim()) return;
    const payload = {
      title: createForm.title.trim(),
      description: createForm.description.trim() || undefined,
      priority: createForm.priority,
      todo_date: createForm.todo_date || undefined,
    };
    try {
      const res = await createTodo(payload);
      setTodos((p) => [res, ...p]);
      setShowAddModal(false);
      setCreateForm({ title: "", description: "", priority: "moderate", todo_date: "" });
    } catch (err) {
      console.error("createTodo error:", err);
    }
  };

  // update
  const handleUpdate = async (id: string, payload: Partial<Todo>) => {
    try {
      const res = await updateTodo(id, payload);
      setTodos((p) => p.map((t) => (String(t.id) === String(id) ? res : t)));
      setEditingTodo(null);
    } catch (err) {
      console.error("updateTodo error:", err);
    }
  };

  // delete
  const handleDelete = async (id: string) => {
    try {
      await deleteTodo(id);
      setTodos((p) => p.filter((t) => String(t.id) !== String(id)));
    } catch (err) {
      console.error("deleteTodo error:", err);
    }
  };

  // edit click
  const handleEditClick = (todo: Todo) => {
    setEditingTodo(todo);
    setEditForm({
      title: todo.title || "",
      description: todo.description || "",
      priority: todo.priority || "moderate",
      todo_date: todo.todo_date ? new Date(todo.todo_date).toISOString().split("T")[0] : "",
    });
  };

  // save edit
  const handleSaveEdit = async () => {
    if (!editingTodo) return;
    if (!editForm.title.trim()) return;
    await handleUpdate(String(editingTodo.id), {
      title: editForm.title.trim(),
      description: editForm.description.trim() || undefined,
      priority: editForm.priority,
      todo_date: editForm.todo_date || undefined,
    });
  };

  // toggle complete
  const handleToggleComplete = async (todo: Todo) => {
    await handleUpdate(String(todo.id), { is_completed: !todo.is_completed });
  };

  if (loading) {
    return (
      <div className="py-12 text-center">
        <div className="w-8 h-8 mx-auto rounded-full animate-spin border-b-2 border-blue-500" />
        <p className="mt-3 text-gray-600">Loading todosâ€¦</p>
      </div>
    );
  }

  return (
    <div>
      {/* Search and Filter Bar */}
      <div className="flex items-center gap-4 mb-6">
        <div className="flex-1 relative">
          <input
            type="text"
            className="w-full border border-gray-300 rounded-lg px-4 py-3 pr-12 text-gray-700 placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="Search your task here..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <div className="absolute right-3 top-1/2 -translate-y-1/2">
            <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>
        </div>

        <div className="relative">
          <button
            onClick={() => setFilterOpen(!filterOpen)}
            className="flex items-center gap-2 px-4 py-3 border border-gray-300 rounded-lg bg-white text-gray-700 hover:bg-gray-50 transition"
          >
            <span>Sort by</span>
            <svg className={`w-4 h-4 transition-transform ${filterOpen ? "rotate-180" : ""}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
            </svg>
          </button>

          {filterOpen && (
            <>
              <div
                className="fixed inset-0 z-10"
                onClick={() => setFilterOpen(false)}
              />
              <div className="absolute right-0 mt-2 w-64 bg-white border border-gray-200 rounded-lg shadow-lg z-20 p-4">
                <h4 className="font-semibold text-gray-900 mb-3">Date</h4>
                <div className="space-y-2">
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={dateFilter.includes("today")}
                      onChange={() => toggleDateFilter("today")}
                      className="w-4 h-4 text-blue-600 rounded"
                    />
                    <span className="text-sm text-gray-700">Deadline Today</span>
                  </label>
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={dateFilter.includes("5days")}
                      onChange={() => toggleDateFilter("5days")}
                      className="w-4 h-4 text-blue-600 rounded"
                    />
                    <span className="text-sm text-gray-700">Expires in 5 days</span>
                  </label>
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={dateFilter.includes("10days")}
                      onChange={() => toggleDateFilter("10days")}
                      className="w-4 h-4 text-blue-600 rounded"
                    />
                    <span className="text-sm text-gray-700">Expires in 10 days</span>
                  </label>
                  <label className="flex items-center gap-2 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={dateFilter.includes("30days")}
                      onChange={() => toggleDateFilter("30days")}
                      className="w-4 h-4 text-blue-600 rounded"
                    />
                    <span className="text-sm text-gray-700">Expires in 30 days</span>
                  </label>
                </div>
              </div>
            </>
          )}
        </div>

        <button
          onClick={() => setShowAddModal(true)}
          className="bg-[#5272FF] text-white px-6 py-3 rounded-lg shadow-md hover:bg-[#4262EF] transition font-medium"
        >
          + New Task
        </button>
      </div>

      {/* Tasks list / empty state */}
      <div className="mt-6 overflow-hidden">
        {filteredTodos.length === 0 ? (
          <div className="rounded-xl border border-dashed border-gray-300 p-12 text-center text-gray-500">
            <div className="mb-3 flex justify-center">
              <div className="relative">
                <svg className="w-24 h-24 text-gray-300" viewBox="0 0 24 24" fill="none">
                  <path d="M9 3h6v4H9zM5 7h14v12a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V7z" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round"/>
                </svg>
                <div className="absolute -bottom-2 -right-2 w-8 h-8 bg-blue-500 rounded-full flex items-center justify-center">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 4v16m8-8H4" />
                  </svg>
                </div>
              </div>
            </div>
            <h3 className="text-xl font-semibold text-gray-700">No todos yet</h3>
            <p className="text-sm mt-2">Create your first todo to get started!</p>
          </div>
        ) : (
          <>
            <h2 className="text-lg font-semibold text-gray-900 mb-4">Your Tasks</h2>
            <div className="overflow-hidden">
              <DndContext 
                sensors={sensors} 
                collisionDetection={closestCorners} 
                onDragEnd={handleDragEnd}
              >
                <SortableContext 
                  items={filteredTodos.map((t) => t.id)} 
                  strategy={rectSortingStrategy}
                >
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {filteredTodos.map((todo) => (
                      <SortableItem
                        key={todo.id}
                        todo={todo}
                        onEdit={handleEditClick}
                        onDelete={handleDelete}
                        onToggleComplete={handleToggleComplete}
                      />
                    ))}
                  </div>
                </SortableContext>
              </DndContext>
            </div>
          </>
        )}
      </div>

      {/* Add Modal */}
      {showAddModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/30" onClick={() => setShowAddModal(false)}>
          <div className="bg-white w-full max-w-2xl rounded-2xl p-8 shadow-xl" onClick={(e) => e.stopPropagation()}>
            <div className="flex items-start justify-between mb-6">
              <h3 className="text-2xl font-semibold text-gray-900">Add New Task</h3>
              <div className="border-b-2 border-[#5272FF] w-[100px] absolute top-12 left-8"></div>
              <button onClick={() => setShowAddModal(false)} className="text-gray-500 hover:text-gray-700 text-sm">Go Back</button>
            </div>

            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Title</label>
                <input
                  value={createForm.title}
                  onChange={(e) => setCreateForm((p) => ({ ...p, title: e.target.value }))}
                  className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Enter task title"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Date</label>
                <div className="relative">
                  <input
                    ref={dateInputRef}
                    type="date"
                    value={createForm.todo_date}
                    onChange={(e) => setCreateForm((p) => ({ ...p, todo_date: e.target.value }))}
                    className="w-full border border-gray-300 rounded-lg px-4 py-3 pr-10 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                  <button
                    type="button"
                    onClick={() => dateInputRef.current?.showPicker()}
                    className="absolute right-3 top-1/2 -translate-y-1/2 cursor-pointer p-1 hover:bg-gray-100 rounded transition"
                  >
                    <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                    </svg>
                  </button>
                </div>
              </div>

              <div className="">
                <label className="block text-sm font-medium text-gray-700 mb-3">Priority</label>
                <div className=" flex s">
                  <label className="flex items-center gap-3 cursor-pointer p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                    <div className="flex items-center gap-2 flex-1">
                      <div className="w-3 h-3 rounded-full bg-red-500"></div>
                      <span className="text-gray-700">Extreme</span>
                    </div>
                    <div className="relative">
                      <input
                        type="radio"
                        name="priority"
                        value="extreme"
                        checked={createForm.priority === "extreme"}
                        onChange={(e) => setCreateForm((p) => ({ ...p, priority: e.target.value as any }))}
                        className="sr-only"
                      />
                      <div className={`w-5 h-5 border-2 rounded flex items-center justify-center ${
                        createForm.priority === "extreme" 
                          ? "border-red-500 bg-red-500" 
                          : "border-gray-300 bg-white"
                      }`}>
                        {createForm.priority === "extreme" && (
                          <svg className="w-3 h-3 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="3" d="M5 13l4 4L19 7" />
                          </svg>
                        )}
                      </div>
                    </div>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                    <div className="flex items-center gap-2 flex-1">
                      <div className="w-3 h-3 rounded-full bg-green-500"></div>
                      <span className="text-gray-700">Moderate</span>
                    </div>
                    <div className="relative">
                      <input
                        type="radio"
                        name="priority"
                        value="moderate"
                        checked={createForm.priority === "moderate"}
                        onChange={(e) => setCreateForm((p) => ({ ...p, priority: e.target.value as any }))}
                        className="sr-only"
                      />
                      <div className={`w-5 h-5 border-2 rounded flex items-center justify-center ${
                        createForm.priority === "moderate" 
                          ? "border-green-500 bg-green-500" 
                          : "border-gray-300 bg-white"
                      }`}>
                        {createForm.priority === "moderate" && (
                          <svg className="w-3 h-3 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="3" d="M5 13l4 4L19 7" />
                          </svg>
                        )}
                      </div>
                    </div>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                    <div className="flex items-center gap-2 flex-1">
                      <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                      <span className="text-gray-700">Low</span>
                    </div>
                    <div className="relative">
                      <input
                        type="radio"
                        name="priority"
                        value="low"
                        checked={createForm.priority === "low"}
                        onChange={(e) => setCreateForm((p) => ({ ...p, priority: e.target.value as any }))}
                        className="sr-only"
                      />
                      <div className={`w-5 h-5 border-2 rounded flex items-center justify-center ${
                        createForm.priority === "low" 
                          ? "border-yellow-500 bg-yellow-500" 
                          : "border-gray-300 bg-white"
                      }`}>
                        {createForm.priority === "low" && (
                          <svg className="w-3 h-3 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="3" d="M5 13l4 4L19 7" />
                          </svg>
                        )}
                      </div>
                    </div>
                  </label>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Task Description</label>
                <textarea
                  rows={6}
                  value={createForm.description}
                  onChange={(e) => setCreateForm((p) => ({ ...p, description: e.target.value }))}
                  className="w-full border border-gray-300 rounded-lg px-4 py-3 resize-none focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Start writing here......"
                />
              </div>
            </div>

            <div className="flex items-center justify-between mt-8">
              <button
                onClick={handleCreate}
                className="px-8 py-3 rounded-lg bg-[#5272FF] text-white font-medium hover:bg-[#4262EF] transition"
              >
                Done
              </button>
              <button
                onClick={() => {
                  setShowAddModal(false);
                  setCreateForm({ title: "", description: "", priority: "moderate", todo_date: "" });
                }}
                className="p-3 rounded-lg bg-red-50 text-red-600 hover:bg-red-100 transition"
                title="Cancel"
              >
                <svg className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                  <path d="M3 6h18M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                </svg>
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Edit Modal */}
      {editingTodo && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/30" onClick={() => setEditingTodo(null)}>
          <div className="bg-white w-full max-w-2xl rounded-2xl p-8 shadow-xl max-h-[90vh] overflow-y-auto" onClick={(e) => e.stopPropagation()}>
            <div className="flex items-start justify-between mb-6">
              <h3 className="text-2xl font-semibold text-gray-900">Edit Task</h3>
              <div className="border-b-2 border-[#5272FF] w-[100px] absolute top-12 left-8"></div>
              <button onClick={() => setEditingTodo(null)} className="text-gray-500 hover:text-gray-700 text-sm">Go Back</button>
            </div>

            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Title</label>
                <input
                  value={editForm.title}
                  onChange={(e) => setEditForm((p) => ({ ...p, title: e.target.value }))}
                  className="w-full border border-gray-300 rounded-lg px-4 py-3 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Enter task title"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Date</label>
                <div className="relative">
                  <input
                    ref={editDateInputRef}
                    type="date"
                    value={editForm.todo_date}
                    onChange={(e) => setEditForm((p) => ({ ...p, todo_date: e.target.value }))}
                    className="w-full border border-gray-300 rounded-lg px-4 py-3 pr-10 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  />
                  <button
                    type="button"
                    onClick={() => editDateInputRef.current?.showPicker()}
                    className="absolute right-3 top-1/2 -translate-y-1/2 cursor-pointer p-1 hover:bg-gray-100 rounded transition"
                  >
                    <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                    </svg>
                  </button>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-3">Priority</label>
                <div className="space-y-3">
                  <label className="flex items-center gap-3 cursor-pointer p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                    <div className="flex items-center gap-2 flex-1">
                      <div className="w-3 h-3 rounded-full bg-red-500"></div>
                      <span className="text-gray-700">Extreme</span>
                    </div>
                    <div className="relative">
                      <input
                        type="radio"
                        name="edit-priority"
                        value="extreme"
                        checked={editForm.priority === "extreme"}
                        onChange={(e) => setEditForm((p) => ({ ...p, priority: e.target.value as any }))}
                        className="sr-only"
                      />
                      <div className={`w-5 h-5 border-2 rounded flex items-center justify-center ${
                        editForm.priority === "extreme" 
                          ? "border-red-500 bg-red-500" 
                          : "border-gray-300 bg-white"
                      }`}>
                        {editForm.priority === "extreme" && (
                          <svg className="w-3 h-3 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="3" d="M5 13l4 4L19 7" />
                          </svg>
                        )}
                      </div>
                    </div>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                    <div className="flex items-center gap-2 flex-1">
                      <div className="w-3 h-3 rounded-full bg-green-500"></div>
                      <span className="text-gray-700">Moderate</span>
                    </div>
                    <div className="relative">
                      <input
                        type="radio"
                        name="edit-priority"
                        value="moderate"
                        checked={editForm.priority === "moderate"}
                        onChange={(e) => setEditForm((p) => ({ ...p, priority: e.target.value as any }))}
                        className="sr-only"
                      />
                      <div className={`w-5 h-5 border-2 rounded flex items-center justify-center ${
                        editForm.priority === "moderate" 
                          ? "border-green-500 bg-green-500" 
                          : "border-gray-300 bg-white"
                      }`}>
                        {editForm.priority === "moderate" && (
                          <svg className="w-3 h-3 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="3" d="M5 13l4 4L19 7" />
                          </svg>
                        )}
                      </div>
                    </div>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer p-3 border border-gray-200 rounded-lg hover:bg-gray-50 transition">
                    <div className="flex items-center gap-2 flex-1">
                      <div className="w-3 h-3 rounded-full bg-yellow-500"></div>
                      <span className="text-gray-700">Low</span>
                    </div>
                    <div className="relative">
                      <input
                        type="radio"
                        name="edit-priority"
                        value="low"
                        checked={editForm.priority === "low"}
                        onChange={(e) => setEditForm((p) => ({ ...p, priority: e.target.value as any }))}
                        className="sr-only"
                      />
                      <div className={`w-5 h-5 border-2 rounded flex items-center justify-center ${
                        editForm.priority === "low" 
                          ? "border-yellow-500 bg-yellow-500" 
                          : "border-gray-300 bg-white"
                      }`}>
                        {editForm.priority === "low" && (
                          <svg className="w-3 h-3 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="3" d="M5 13l4 4L19 7" />
                          </svg>
                        )}
                      </div>
                    </div>
                  </label>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">Task Description</label>
                <textarea
                  rows={6}
                  value={editForm.description}
                  onChange={(e) => setEditForm((p) => ({ ...p, description: e.target.value }))}
                  className="w-full border border-gray-300 rounded-lg px-4 py-3 resize-none focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Start writing here......"
                />
              </div>
            </div>

            <div className="flex items-center justify-between mt-8">
              <button
                onClick={handleSaveEdit}
                className="px-8 py-3 rounded-lg bg-[#5272FF] text-white font-medium hover:bg-[#4262EF] transition"
              >
                Save Changes
              </button>
              <button
                onClick={() => setEditingTodo(null)}
                className="p-3 rounded-lg bg-red-50 text-red-600 hover:bg-red-100 transition"
                title="Cancel"
              >
                <svg className="w-5 h-5" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                  <path d="M3 6h18M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                </svg>
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
